import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
// import App from "./App";
import Home from "./core/Home";
import NotFound from "./NotFound";
import Productdets from "./core/Productdets";
import Signin from "./user/Signin";
import Signup from "./user/Signup";
import Cart from "./core/Cart";
import UserDashBoard from "./user/UserDashBoard";
import PrivateRoutes from "./auth/helper/PrivateRoutes";
import OrderSubmit from "./core/OrderSubmit";
import Address from "./core/Address";
import PaymentB from "./core/PaymentB";
import MyOrder from "./user/MyOrder";
import SellerOrder from "./user/SellerOrder";
import AddAddress from "./core/AddAddress";
import AboutUs from "./core/AboutUs";
import helpus from "./core/helpus";
import CallSupport from "./core/CallSupport";
import SellYourProduct from "./core/SellYourProduct";

const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        {/* <Route path="/" exact component={App} /> */}
        <Route path="/" exact component={Home} />
        <Route path="/cart" exact component={Cart} />
        <Route path="/user/login" exact component={Signin} />
        <Route path="/user/signup" exact component={Signup} />
        <Route path="/product/:id/" exact component={Productdets} />
        <Route path="/aboutus" exact component={AboutUs} />
        <Route path="/helpus" exact component={helpus} />
        <Route path="/callsupport" exact component={CallSupport} />
        <PrivateRoutes path="/user/dashboard" exact component={UserDashBoard} />
        <PrivateRoutes path="/order/address" exact component={Address} />
        <PrivateRoutes path="/order/address/add" exact component={AddAddress} />
        <PrivateRoutes path="/order/confirm" exact component={OrderSubmit} />
        <PrivateRoutes path="/user/order/myorder" exact component={MyOrder} />
        <PrivateRoutes
          path="/user/product/add"
          exact
          component={SellYourProduct}
        />

        <PrivateRoutes
          path="/user/order/sellorder"
          exact
          component={SellerOrder}
        />
        <PrivateRoutes
          path="/order/confirm/payment"
          exact
          component={PaymentB}
        />

        <Route component={NotFound} />
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
